import React from "react";
import Layout from "./Layout";
import CountUp from "react-countup";

import { FiDownload } from "react-icons/fi";
import { Button, buttonVariants } from "../components/ui/button";
import { TypeAnimation } from "react-type-animation";
import { motion } from "framer-motion";

import Social from "../components/Social";
import Photo from "../components/Photo";
import Card from "../components/Card";

import Gambar from "../assets/Logo/Logo4.jpg";
import Baner from "../assets/Logo/Logo5.jpg";

const About = () => {
  return (
    <Layout>
      <section className="h-full">
        <div className="container">
          <div
            className="grid grid-cols-1 xl:grid-cols-2 items-center justify-between xl:pt-8 
        xl:pb-24"
          >
            {/*bagian teks*/}
            <div className="text-center xl:text-left order-2 xl:order-none">
              <span className="text-xl">"Nihongo Bisa!"</span>
              <h1 className="h1 text-second">
                Develop Your language in a new and unique way
              </h1>
              <p className="max-w-[500px] mb-9 text-black/80">
                Selamat datang di kursus belajar Bahasa Jepang kami! Kursus ini
                dirancang untuk membantu Anda memahami dan menguasai dasar-dasar
                Bahasa Jepang, mulai dari kosakata, tata bahasa, hingga
                keterampilan berbicara dan menulis
              </p>
              {/*Tombol sosial media*/}
              <div className="flex flex-col xl:flex-row items-center gap-8">
                <a href="#" download="My_CV.pdf">
                  <Button
                    variant="default"
                    size="lg"
                    className="uppercase flex items-center gap-2"
                  >
                    <span>Our SPAN</span>
                    <FiDownload className="text-xl" />
                  </Button>
                </a>
                <div className="mb-8 xl:mb-0">
                  <Social
                    containerStyles="flex gap-6"
                    iconStyles=" w-14 h-14 border
                border-primary rounded-full flex justify-center items-center text-accent
                text-base hover:bg-second hover:text-primary 
                hover:transition-all duration-500 "
                  />
                </div>
              </div>
            </div>
            {/*bagian photo*/}
            <div className=" xl:order-none mb-8 xl:mb-0">
              <Photo />
            </div>
          </div>
        </div>
      </section>

      <section className="container">
        <div
          className="container rounded-md"
          style={{ backgroundImage: `url(${Baner})` }}
        >
          <div className="grid grid-cols-1 xl:grid-cols-3 gap-4">
            <div>
              <div className="border-4 p-1 xl:p-4 backdrop-blur-md rounded-md flex flex-col text-center">
                <h1 className="h3 text-white">
                  <CountUp end={1000} duration={2} />
                </h1>
                <h1 className="h3 text-white">
                  <CountUp end={2000} duration={2.5} />
                </h1>
                <h1 className="h3 text-white">
                  <CountUp end={3000} duration={3} />
                </h1>
                <h1 className="h3 text-white">
                  <CountUp end={4000} duration={3.5} />
                </h1>
                <h1 className="h3 text-white">
                  <CountUp end={5000} duration={4} />
                </h1>
              </div>
            </div>
            <div>
              <div className="border-4 p-1 xl:p-4 backdrop-blur-md rounded-md flex flex-col text-center">
                <h1 className="h3 text-white">
                  <CountUp end={6000} duration={2} />
                </h1>
                <h1 className="h3 text-white">
                  <CountUp end={7000} duration={2.5} />
                </h1>
                <h1 className="h3 text-white">
                  <CountUp end={8000} duration={3} />
                </h1>
                <h1 className="h3 text-white">
                  <CountUp end={9000} duration={3.5} />
                </h1>
                <h1 className="h3 text-white">
                  <CountUp end={10000} duration={4} />
                </h1>
              </div>
            </div>
            <div>
              <div className="border-4 p-1 xl:p-4 backdrop-blur-md rounded-md flex flex-col text-center">
                <h1 className="h3 text-white">
                  <CountUp end={11000} duration={2} />
                </h1>
                <h1 className="h3 text-white">
                  <CountUp end={12000} duration={2.5} />
                </h1>
                <h1 className="h3 text-white">
                  <CountUp end={13000} duration={3} />
                </h1>
                <h1 className="h3 text-white">
                  <CountUp end={14000} duration={3.5} />
                </h1>
                <h1 className="h3 text-white">
                  <CountUp end={15000} duration={4} />
                </h1>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section className="container">
        <div
          className="container rounded-md"
          style={{ backgroundImage: `url(${Baner})` }}
        >
          <div className="container">
            <div
              className="container rounded-md"
              style={{ backgroundImage: `url(${Gambar})` }}
            >
              <h1 className="text-sm md:text-xl xl:text-6xl font-bold xl:h1 text-center text-white">
                <motion.div
                  className="inline-block" // Tambahkan class inline-block di sini
                  initial={{ opacity: 0, scale: 0.5 }}
                  animate={{ opacity: 1, scale: 1 }}
                  transition={{ duration: 0.5 }}
                >
                  <p>
                    {" "}
                    <TypeAnimation
                      sequence={[
                        "Kursus Populer Kami",
                        1000,
                        "Our Popular Course",
                        1000,
                        "人気のコース",
                        1000,
                      ]}
                      wrapper="span"
                      speed={30}
                      repeat={Infinity}
                    />
                  </p>
                </motion.div>
              </h1>
            </div>
          </div>
          <Card />
        </div>
      </section>
    </Layout>
  );
};

export default About;
