import React from "react";
import Dimsum from "../assets/Logo/Logo.png";

const datacourse = [
  {
    nama: "Course 1",
    url: "http://",
    harga: "2xx.xxx,xx",
    star: "5.5",
  },
  {
    nama: "Course 2",
    url: "http://",
    harga: "2xx.xxx,xx",
    star: "5.5",
  },
  {
    nama: "Course 3",
    url: "http://",
    harga: "2xx.xxx,xx",
    star: "5.5",
  },
];

const Card = () => {
  return (
    <section className="container grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-3">
      {datacourse.map((data, index) => {
        return (
          <div className="flex flex-col rounded-xl bg-white bg-clip-border text-gray-700">
            <div className="mx-4 mt-4 overflow-hidden rounded-xl bg-blue-gray-500 bg-clip-border text-white">
              <img src={Dimsum} alt="ui/ux review check" />
            </div>
            <div className="p-6">
              <div className="mb-3 flex items-center justify-between">
                <h5 className="block font-sans text-xl font-medium leading-snug tracking-normal text-blue-gray-900 antialiased">
                  {data.nama}
                </h5>
                <p className="flex items-center gap-1.5 font-sans text-base font-normal leading-relaxed text-blue-gray-900 antialiased">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 24 24"
                    fill="currentColor"
                    aria-hidden="true"
                    className="-mt-0.5 h-5 w-5 text-yellow-700"
                  >
                    <path
                      fillRule="evenodd"
                      d="M10.788 3.21c.448-1.077 1.976-1.077 2.424 0l2.082 5.007 5.404.433c1.164.093 1.636 1.545.749 2.305l-4.117 3.527 1.257 5.273c.271 1.136-.964 2.033-1.96 1.425L12 18.354 7.373 21.18c-.996.608-2.231-.29-1.96-1.425l1.257-5.273-4.117-3.527c-.887-.76-.415-2.212.749-2.305l5.404-.433 2.082-5.006z"
                      clipRule="evenodd"
                    ></path>
                  </svg>
                  {data.star}
                </p>
              </div>

              <h5 className="block font-sans text-md font-medium leading-snug tracking-normal text-blue-gray-900 antialiased">
                Rp. {data.harga}
              </h5>

              <p>
                Lorem ipsum dolor sit, amet consectetur adipisicing elit. Quam
                laudantium adipisci nam.
              </p>

              <p className="block font-sans text-base font-light leading-relaxed text-gray-700 antialiased"></p>
              <div class="pt-3">
                <button
                  class="block w-full select-none rounded-lg bg-accent py-3.5 px-7 text-center align-middle font-sans text-sm font-bold uppercase text-black shadow-md shadow-pink-500/20 transition-all hover:shadow-lg hover:shadow-pink-500/40 focus:opacity-[0.85] focus:shadow-none active:opacity-[0.85] active:shadow-none disabled:pointer-events-none disabled:opacity-50 disabled:shadow-none"
                  type="button"
                  data-ripple-light="true"
                >
                  CONTACT US
                </button>
              </div>
            </div>
          </div>
        );
      })}
    </section>
  );
};

export default Card;
